  background: -webkit-gradient(linear, left top, right top, from(#83b7f5), to(#292e33));
  background: linear-gradient(90deg, #83b7f5 0%, #292e33 100%);
}
html, body, #app {
  height: 100%;
  min-height: 100%;
}
h1, .song, .song .song_name {
  color: #fff;
}
h1, h2, h3, h4, h5 {
  font-family: "YellowTail", serif;
}
a {
  color: white;
}
a:hover {
    color: white;
}
.heart-icon {
  margin-left: 0.2em;
  color: #00ffcf;
  font-size: 0.9em;
}
.download-song {
  cursor: pointer;
}
.table {
  background-color: rgba(255, 255, 255, 0.7);
  color: #000;
}
iframe {
  width: 100%;
}
.top-header {
  background-color: black;
  height: 60px;
  position: fixed;
  width: 84%;
  padding-right: 1em;
  z-index: 999;
}
h5, .badge, p, div {
  font-family: 'Source Sans Pro', serif;
  font-weight: 300;
}
.song {
  padding: 1rem 1.5rem;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}
.song:hover {
    background: #647b8e;
}
.song:hover h5 {
    color: white;
}
.song.active-song {
    background: #647b8e;
}
.song .song-detail {
    width: 71%;
}
.song .song-detail .song_thumbnail {
      width: 45px;
      height: 45px;
      cursor: pointer;
}
.song .song-detail .song_name {
      padding: 0;
}
.song .song-detail .song_name h5, .song .song-detail .song_name .artist, .song .song-detail .song_name .badge {
        font-family: 'Source Sans Pro', serif;
        font-weight: 300;
}
.song .song-detail .song_name h5 {
        display: inline-block;
        width: 100%;
        font-size: 1.1rem;
        -webkit-transition: all 0.3s ease-out;
        transition: all 0.3s ease-out;
        border-bottom: 1px solid transparent;
}
.song .song-detail .song_name h5 .badge {
          margin-left: 0.2rem;
}
.song .song-detail .song_name .artist {
        display: inline-block;
        color: #b8e3ff;
}
.song .song-actions {
    width: 29%;
    font-size: 1.5em;
}
.song .song-actions .play-btn {
      cursor: pointer;
}
.song .song-actions .song-actions-dropdown button {
      background-color: transparent;
      border: none;
      -webkit-appearance: none;
      outline: none;
      -webkit-box-shadow: none;
              box-shadow: none;
}
.song .song-actions .song-actions-dropdown button::before {
        border: none;
}
.song:hover .song_name h5 {
  /*border-color: #f1f1f1;*/
}
.song .song_num {
  width: 5%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.song .song_num > * {
  margin: 0;
}
.song .song_name > * {
  margin: 0;
}
.songs-section {
  margin-top: 85px;
}
.prev_iframe {
  position: fixed;
  top: 0;
  right: 0;
  height: 300px;
  z-index: 9999;
}
.padd_top {
  padding-top: 350px;
}
#topbutton {
  position: fixed;
  opacity: 0;
  overflow: hidden;
  text-align: center;
  z-index: 99999999;
  background-color: #777777;
  color: #eeeeee;
  width: 50px;
  height: 48px;
  line-height: 48px;
  right: 30px;
  bottom: 30px;
  padding-top: 2px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  cursor: pointer;
}
#topbutton.show {
  visibility: visible;
  cursor: pointer;
  opacity: 1.0;
}
#topbutton.hide {
  visibility: hidden;
}
#topbutton .fa {
  padding-top: 6px;
}
.prev_iframe .close-video {
  display: inline-block;
  position: absolute;
  right: 32px;
  top: 0;
  color: white;
  font-size: 45px;
}
.prev_iframe .close-video a {
  color: white;
}

/**
input[type=checkbox]:checked + label img
{
  border: 1px solid blue;
}
 */
.selected {
  border: 1px solid blue;
}
.img_modal {
  position: relative;
}
.img_modal .modal_btn, .bs_datepicker {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.img_modal .modal_btn button {
  height: 100%;
  opacity: 0;
}
.bs_datepicker input {
  height: 100%;
  opacity: 0;
}
.filter-buttons button {
  margin-right: 3px;
}
.music-alert-box {
  -webkit-transition: opacity 0.3s, height 0.3s 0.1s;
  transition: opacity 0.3s, height 0.3s 0.1s;
  height: 0;
}
.music-alert-box .alert {
    opacity: 0;
}
.music-alert-box.alert-active {
    height: 75px;
    -webkit-transition: opacity 0.3s 0.1s, height 0.3s;
    transition: opacity 0.3s 0.1s, height 0.3s;
}
.music-alert-box.alert-active .alert {
      opacity: 1;
}
#sticky-sidebar {
  background: black;
}
#sticky-sidebar #side-col .list-filters {
    margin-bottom: 0.5rem;
}
#sticky-sidebar #side-col .list-filters .list-group-item {
      border: none;
      background-color: transparent;
}
#sticky-sidebar #side-col .list-filters .list-group-item-action {
      color: #bdcad6;
}
#sticky-sidebar #side-col .list-filters .list-group-item-action:hover {
        color: white;
}
#sticky-sidebar #side-col .list-filters .list-group-item-action.active-cat {
        background: #33434d;
        color: white;
}
.fancy-title {
  font-family: 'YellowTail', serif;
  font-size: 2rem;
  color: white;
}
.fancy-title.h1 {
    font-size: 3rem;
}
@media (max-width: 768px) {
.music {
    padding: 0 0;
}
.song img {
    margin: 0 15px;
}
.song .song_name {
    width: 100%;
}
}

